html {
  background: url(/pastforward/static/media/background.b6243e71.jpg);
  background-size: cover;
  font-size: 16px;
  color: rgb(151, 141, 122);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media(min-width:60em) {
  html {
    font-size: 100%
  }
}

html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: auto;
}

p {
  font-size: 250%;
  line-height: 1.2;
}

a {
  color: rgb(151, 141, 122);
  text-decoration: none;
}

h1 {
  font-size: 500%;
  font-weight: normal;
  display: inline-block;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
}

@media only screen and (max-width: 768px) {
  h1 {
    font-size: 350%;
  }
}

@font-face {
  font-family: 'GermaniaVersalien';
  font-display: auto;
  src: url(/pastforward/static/media/GermaniaVersalien.d36b44b6.ttf) format('truetype');
}

@font-face {
  font-family: 'DistortionDosAnalogue';
  font-display: auto;
  src: url(/pastforward/static/media/DistortionDosAnalogue.8faf7581.otf) format('truetype');
}

.App-header {
  margin: 0 0 0 5vh;
  color: white;
}

.App-past {
  font-family: GermaniaVersalien, arial, sans-serif;
  margin: 0 1vh 0 0;
}

.App-forward {
  font-family: DistortionDosAnalogue, arial, sans-serif;
  margin: 0;
}

.App-forward:after {
  content: '\23E9';
  height: 28px;
  width: 28px;
  opacity: 0.75;
}

.App-About {
  float: right;
  font-size: 2em;
  margin: 2vh 2vh 0 0;
}

.App-About:before {
  content: "\24D8";
}

.ImageList {
  position: relative;
  display: block;
  width: 100%;
  height: 100vh;
  float: left;
}
.Image {
  display: block;
  position: absolute;
  z-index: 1;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
  top: 0px;
  left: 0px;
  width: 50%;
  height: auto;
  border: 0.75rem transparent solid;
  background-color: #eee;
  margin: 0;
  padding: 0;
  touch-action: none;
}
@font-face {
  font-family: 'Berolina';
  font-display: auto;
  src: url(/pastforward/static/media/Berolina.d61b8938.ttf) format('truetype');
}

.Video,
.about-Container {
  overflow: auto;
  margin: 0;
  overflow-x: hidden;
}

video {
  position: relative;
  width: 100%;
  height: 35em;
  z-index: 9999;
  overflow: hidden;
}

.intro,
.end,
.about {
  position: relative;
  z-index: 9999;
  background: url(/pastforward/static/media/intro.c22cc70d.jpg);
  width: 100%;
  background-size: 100% 100%;
}

.intro-Text {
  display: block;
  font-family: Berolina, arial, sans-serif;
  text-align: center;
  font-size: 4vw;
  padding: 12vw 10vw;
}

.end-Text,
.about-Text {
  display: block;
  font-family: Berolina, arial, sans-serif;
  text-align: center;
  font-size: 2vw;
  padding: 12vw 10vw;
}

.association {
  position: relative;
  z-index: 9999;
  background: url(/pastforward/static/media/intro.c22cc70d.jpg);
  width: 100%;
  background-size: 100% 100%;
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: center;
}

@media only screen and (max-width: 640px) {
  .association {
    flex-direction: column;
  }

  .association-Container:first-child {
    padding-top: 30vw;
  }

  .association-Container:last-child {
    padding-top: 0;
    padding-bottom: 15vw
  }
}

.association-Container {
  font-size: 2vw;
  padding: 20vw 10vw 0;
}

.association-Text {
  font-family: Berolina, arial, sans-serif;
  text-align: center;
}

.association-Image {
  height: 12rem;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

